import React, { useEffect, useState } from 'react';
import lion from '../Camera/lion.png'
import './Camera.scss'
import gorilla from '../../assets/glb/RobotExpressive.glb'
import defaultImg from '../../assets/imgs/default.png'

export function Camera() {

    const [url, setUrl] = useState(null);
    const HEIGHT = 500
    const WIDTH = 500
    var video = document.getElementsByClassName('app__videoFeed')[0];
    // const startVideo = () => {
    console.log('start');
    navigator.getUserMedia(
        {
            video: true,
        },
        (stream) => {
            if (video) {
                video.srcObject = stream;
            }
        },
        (err) => console.error(err)
    );
    // }


    // startVideo()
    useEffect(() => {
        setUrl(defaultImg)
    },[])

    const handleChange = ({ target }) => {
        const file = target.files[0]
        if (file) {
            const reader = new FileReader();
            reader.addEventListener("load", function () {
                console.log(this);
                setUrl(this.result)
            })
            reader.readerAsDataURL(file);
        }
    }

    const takePicture = () => {
        var canvas = document.querySelector('.canvas')
        canvas.style.display = 'none'
        var context = canvas.getContext('2d')
        context.drawImage(video, 0, 0, 500, 500)
        // context.drawImage(lion, 0, 0, 100, 100)
        make_base();
    }

    const make_base = () => {
        var canvas = document.querySelector('.canvas')
        var context = canvas.getContext('2d')
        var base_image = new Image();
        console.log(lion);
        base_image.src = lion;
        base_image.onload = () => {
            // context.drawImage(base_image, 0, 0, 100, 100);
            setUrl(canvas.toDataURL())
        }

    }

    const onFileSelected = (event) => {
        console.log('check');
        var selectedFile = event.target.files[0];
        var reader = new FileReader();

        reader.onload = function (event) {
            setUrl(event.target.result)
            // imgtag.src = event.target.result;
        };

        reader.readAsDataURL(selectedFile);
    }

    return (
        <div className="container">
            <input type="file" onChange={(event) => onFileSelected(event)} />
            <div className="camera">
                <div className="camera-container">
                    <div className="app__container">
                        <video
                            height={HEIGHT}
                            width={WIDTH}
                            muted
                            autoPlay
                            className="app__videoFeed"
                        ></video>
                        <div className="demo">
                            <model-viewer src={gorilla} auto-rotate camera-controls>
                            </model-viewer>
                        </div>
                    </div>
                    {url && <div className="screenshot">
                        <img src={url} alt="" />
                        <div className="demo">
                            <model-viewer src={gorilla} auto-rotate camera-controls>
                            </model-viewer>
                        </div>
                    </div>}
                </div>
                <div className="app__input">
                    <button onClick={takePicture}>Take a picture</button>
                </div>
                <canvas className="canvas" height={HEIGHT} width={WIDTH} ></canvas>
            </div>
        </div>
    );
}

